import * as THREE from 'three';
import SandboxVector from '..';
import { RendererThreeVectorSandboxBase } from './Base/Base'

export class RendererThreeVecSandbox extends RendererThreeVectorSandboxBase {
    public sandboxVector: SandboxVector
    public pointLight: THREE.PointLight

    public rayCaster = new THREE.Raycaster();
    public mouse = new THREE.Vector2();
    public m = new THREE.Vector3();

    public sphere: THREE.Mesh;
    public ground: THREE.Mesh;

    public axis: THREE.AxesHelper;
    public grid: THREE.GridHelper;


    constructor(sandboxVector: SandboxVector, id: string = 'main', color = '#000') {
        super(id)
        THREE.Object3D.DEFAULT_UP.set(0, 1, 0);
        this.sandboxVector = sandboxVector;
        this.setBG(color);

        this.initPost();
        this.start();
    }
    public initPost() {

        const ambientLight = new THREE.AmbientLight(0x555555);
        this.scene.add(ambientLight);

        this.pointLight = new THREE.PointLight(0xffffff, 100);
        this.pointLight.position.x = 10;
        this.pointLight.castShadow = true;
        this.scene.add(this.pointLight);

        this.axis = new THREE.AxesHelper(5);
        this.axis.position.z = 0.001;
        // this.axis.rotation.y = -Math.PI / 2;
        this.scene.add(this.axis);

        const sizeGrid = 50
        this.grid = new THREE.GridHelper(sizeGrid, 100, 0x444444, 0x323232);
        this.grid.rotation.x = -Math.PI / 2;
        this.scene.add(this.grid);

        const geometry = new THREE.PlaneGeometry(sizeGrid, sizeGrid);
        const materialG = new THREE.MeshBasicMaterial({color: 0x010101, side: THREE.DoubleSide, 
            transparent: true, opacity: 0
        });
        this.ground = new THREE.Mesh(geometry, materialG);
        // this.ground.rotation.x = -Math.PI / 2; // Rotate to make it flat
        // this.ground.position.set(0,-1,0)
        this.scene.add(this.ground);

        
        // @ts-ignore
        const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
        const geometrySphere = new THREE.SphereGeometry(0.05, 50, 50);
        const objectSphere = new THREE.Mesh(geometrySphere, material);
        objectSphere.translateX(1)
        this.sphere = objectSphere
        this.scene.add(objectSphere);


        // let geometry = new THREE.SphereGeometry(1, 50, 50);
        // let object = new THREE.Mesh(geometry, new THREE.MeshPhongMaterial());
        // object.translateX(-1)
        // this.add(object);

        // geometry = new THREE.SphereGeometry(1, 50, 50);
        // object = new THREE.Mesh(geometry, new THREE.MeshPhongMaterial());
        // object.translateX(1)
        // this.add(object);
    }
    public reset() {

    }
    set showGrid(v: boolean) {
        this.grid.visible = v;
    }
    set showAxis(v: boolean) {
        this.axis.visible = v;
    }
    public picking(e: MouseEvent, arrayMesh) {
        this.mouse.x = (e.offsetX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = -(e.offsetY / this.renderer.domElement.clientHeight) * 2 + 1;

        this.rayCaster.setFromCamera(this.mouse, this.camera);

        const intersects = this.rayCaster.intersectObjects([ ...arrayMesh]);
        if (intersects.length > 0) {
            // console.log(intersects[0].object);
            // console.log(intersects[0].point.x, intersects[0].point.y, intersects[0].point.z)
            this.m.set(intersects[0].point.x, intersects[0].point.y, intersects[0].point.z);

            if (intersects[0].object !== this.sphere) {
                this.sphere.position.set(intersects[0].point.x, intersects[0].point.y, intersects[0].point.z)
            }
            // console.log(intersects[0].object.userData);
            return { 
                intersects, 
                ground: undefined 
            };

        }

        const intersectGround = this.rayCaster.intersectObjects([this.ground]);
        if (intersectGround.length > 0) {
            // console.log(intersectGround)
            this.m.set(intersectGround[0].point.x, intersectGround[0].point.y, intersectGround[0].point.z);
            this.sphere.position.set(intersectGround[0].point.x, intersectGround[0].point.y, intersectGround[0].point.z)
            return {intersects: undefined, ground :intersectGround};
        }

        return undefined;
    }
    public update(scene: THREE.Scene) {
        this.updatePost(scene)

        // if (this.sandboxVector.scene) {
        //     this.sandboxVector.scene.mesh.forEach((m: THREE.Mesh, i) => {
        //         m.position.x = this.sandboxVector.scene.arrayPoint[i].vec.x;
        //         m.position.y = this.sandboxVector.scene.arrayPoint[i].vec.y;
        //         m.position.z = this.sandboxVector.scene.arrayPoint[i].vec.z;
        //     })

        //     this.sandboxVector.scene.arrayArrow.forEach((m: THREE.ArrowHelper, i) => {
        //         m.position.x = this.sandboxVector.scene.arrayPoint[i].vec.x;
        //         m.position.y = this.sandboxVector.scene.arrayPoint[i].vec.y;
        //         m.position.z = this.sandboxVector.scene.arrayPoint[i].vec.z;
        //         const vec = new THREE.Vector3(this.sandboxVector.scene.arrayPoint[i].velocity.x, this.sandboxVector.scene.arrayPoint[i].velocity.y, this.sandboxVector.scene.arrayPoint[i].velocity.z)
        //         const len = vec.length() * 0.5
        //         m.setLength(len, 0.1,  0.1)
        //         m.setDirection(vec)
        //     })

        //     this.sandboxVector.scene.arrayLineMesh.forEach((ln, i) => {
        //         const lnObj = this.sandboxVector.scene.arrayLine[i];
        //         ln.geometry.attributes.position.setXYZ(0, lnObj.p0.vec.x, lnObj.p0.vec.y, lnObj.p0.vec.z);
        //         ln.geometry.attributes.position.setXYZ(1, lnObj.p1.vec.x, lnObj.p1.vec.y, lnObj.p1.vec.z);
        //         ln.geometry.attributes.position.needsUpdate = true;
        //     })
        // }

        const matrix = new THREE.Matrix4();
        matrix.makeRotationY(Math.PI * 0.011);
        this.pointLight.position.applyMatrix4(matrix);
    }
    public updatePost = (scene: THREE.Scene) => { 
        
    }
    // .................................................... Event
    public onMouseClick = (e) => {}
    public mouseClickPre(e: MouseEvent): void {
        const intersect = this.picking(e, this.sandboxVector.scene.mesh);

        if (intersect.intersects && intersect.intersects[0]) {
            // console.log('---------------');
            intersect.intersects[0].object.userData.onClick(intersect.intersects[0])
        }

        // this.sandboxVector.common.updateSelectedData(intersect);
        // this.onMouseClick({e, intersect});
    }
    public onMouseMove = (e) => {}
    public mouseMovePre= (e: MouseEvent) => {
        const intersect = this.picking(e, this.sandboxVector.scene.mesh);
        this.onMouseMove({e, intersect});
    }

}
