export class Vector3 {
    public x: number = 0.0;
    public y: number = 0.0;
    public z: number = 0.0;
    constructor(x, y, z) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
}


// ..................................................................
export const xAxis = () => {
    return new Vector3(1, 0, 0);
}
export const yAxis = () => {
    return new Vector3(0, 1, 0);
}
export const zAxis = () => {
    return new Vector3(0, 0, 1);
}
export const zero = () => {
    return new Vector3(0, 0, 0);
}
export const origin = () => {
    return new Vector3(0, 0, 0);
}
// ..................................................................

export const distance = (v0: Vector3, v1: Vector3) => {
    return Math.sqrt(distance2(v0, v1));
}
export const distance2 = (v0: Vector3, v1: Vector3) => {
    return (v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z);
}
export const dotProduct = (v1: Vector3, v2: Vector3) => {
    return v1.x * v2.x + v1.y * v2.y + v1.z * v2.z;
}
export const crossProduct = (v1: Vector3, v2: Vector3) => {
    return new Vector3(
        v1.y * v2.z - v1.z * v2.y,
        v1.z * v2.x - v1.x * v2.z,
        v1.x * v2.y - v1.y * v2.x
    );
}
export const normalize = (v: Vector3) => {
    let len: number = Math.sqrt(v.x * v.x + v.y * v.y + v.z * v.z);
    if (len < 0.0001) {
        return new Vector3(v.x, v.y, v.z);
    }
    return new Vector3(v.x / len, v.y / len, v.z / len);
}
export const length = (v: Vector3) => {
    return Math.sqrt(v.x * v.x + v.y * v.y + v.z * v.z);
}

// ..................................................................
export const multiply = (v: Vector3, s: number) => {
    return scale(v, s);
}
export const scale = (v: Vector3, s: number) => {
    return new Vector3(v.x * s, v.y * s, v.z * s);
}
export const sub = (v0: Vector3, v1: Vector3) => {
    return new Vector3(v0.x - v1.x, v0.y - v1.y, v0.z - v1.z);
}
export const add = (v0: Vector3, v1: Vector3) => {
    return new Vector3(v0.x + v1.x, v0.y + v1.y, v0.z + v1.z);
}
