import * as THREE from 'three';
import SandBoxVector from '..';
import { Vector3 } from './Vector3'

export class Point3 {
    // private _x: number;
    // private _y: number;
    // private _z: number;

    public vec: Vector3;
    public radius: number = 0.5;

    public material: THREE.MeshBasicMaterial;
    public geometry: THREE.SphereGeometry;
    public mesh: THREE.Mesh;

    // constructor(x: number, y: number, z: number, radius = 0.1) {
    constructor(v: Vector3, radius = 0.1) {
        // this._x = x;
        // this._y = y;
        // this._z = z;
        this.vec = v;
        this.radius = radius;
        this.init();
    }
    // public get x () { return this._x; }
    // public set x (v) { this._x = v; this.updateVec();}
    // public get y () { return this._y; }
    // public set y (v) { this._y = v; this.updateVec();}
    // public get z () { return this._z; }
    // public set z (v) { this._z = v; this.updateVec();}
    // public set(x: number, y: number, z: number) {
    //     this._x = x; this._y = y; this._z = z;
    //     this.updateVec();
    // }
    public destroy() {
        SandBoxVector.app.scene.remove(this.mesh);
    }
    public onClick(data: any) {

        this.update();
    }
    // ...........................................................
    public update() {
        this.mesh.position.set(this.vec.x, this.vec.y, this.vec.z);
    }
    private init() {
        this.material = new THREE.MeshBasicMaterial({ color: 0xffffff });
        this.geometry = new THREE.SphereGeometry(this.radius, 20, 20);
        this.mesh = new THREE.Mesh(this.geometry, this.material);
        this.mesh.position.set(this.vec.x, this.vec.y, this.vec.z);
        this.mesh.userData = this;
        SandBoxVector.app.scene.add(this.mesh);
    }   
}