import { RendererThreeVecSandbox } from './Renderer';
import { Scene } from './Scene';
import { Common } from './Common';

export * from './Geometry';

class NRenderer {
    public static app: NRenderer;
    public id: string;
    public renderer: RendererThreeVecSandbox;
    public common: Common;
    public scene: Scene;
    public themeColor = '#222';

    constructor(id: string, themeColor = '#222') {
        NRenderer.app = this;
        this.id = id;
        this.themeColor = themeColor;

        this.initRenderer();
    }
    public destroy() {
        this.renderer.destroy();
    }
    private initRenderer() {
        this.renderer = new RendererThreeVecSandbox(this, this.id, this.themeColor);
        this.renderer.updatePost = (scene) => this.updateBinding();
        this.renderer.onMouseMove = (e) => this.onMouseMove(e);
        this.renderer.onMouseClick = (e) => this.onMouseClick(e);
        this.scene = new Scene(this);
        this.common = new Common(this)

    }
    public set showAxis(v){
        this.renderer.showAxis = v;
    }
    public set showGrid(v) {
        this.renderer.showGrid = v;
    }
    public reset() {
        this.renderer.reset();
        this.scene.reset();

        this.start();
    }
    // ..........................
    public start() {
        // TODO: 
    }
    public updateBinding() {
        // TODO: 
    }
    public onMouseMove(e: any) { }
    public onMouseClick(e: any) { }
}

export default NRenderer