import * as THREE from 'three';
import SandBoxVector from '..';
import { Vector3 } from './Vector3';

// import { NURBSCurve } from ''

export class SplineCurve {
    public vs: Vector3[] = [];
    public material: THREE.LineBasicMaterial;
    public geometry: THREE.BufferGeometry;
    public mesh;
    public divisions: number = 30

    constructor(vs: Vector3[], divisions = 30) {
        this.vs = vs;
        this.divisions = divisions;
        this.init();
    }
    public onClick() {
        console.log('Curve')
    }
    public update() {
        this.vs.forEach((vec, i) => {
            this.geometry.attributes.position.setXYZ(i, vec.x, vec.y, vec.z);
        });
        this.geometry.attributes.position.needsUpdate = true;
    }
    public init() {
        const vs = this.vs.map((vec) => new THREE.Vector3(vec.x, vec.y, vec.z));
        // console.log(vs)
        const curve =  new THREE.CatmullRomCurve3(vs);
        const points = curve.getPoints(this.divisions);
        this.geometry = new THREE.BufferGeometry().setFromPoints(points);
        this.material = new THREE.LineBasicMaterial({color: 0xff0000 });

        this.mesh = new THREE.Line(this.geometry, this.material);
        // @ts-ignore
        this.mesh.userData = this;
        SandBoxVector.app.scene.add(this.mesh);
    }
}